import { PrismaClient } from '@prisma/client';

const prisma = new PrismaClient();

export async function POST(req) {
  try {
    const { name, email, password } = await req.json();

    if (!email || !password || !name) {
      return new Response(JSON.stringify({ message: 'Missing fields' }), { status: 400 });
    }

    // Check if user already exists
    const existingUser = await prisma.user.findUnique({
      where: { email },
    });

    if (existingUser) {
      return new Response(JSON.stringify({ message: 'User already exists' }), { status: 409 });
    }

    // Create new user
    // NOTE: Password should be hashed in production using bcrypt!
    // Storing as plain text per existing pattern for now.
    const newUser = await prisma.user.create({
      data: {
        name,
        email,
        password, // Plain text password
        role: 'USER',
      },
    });

    return new Response(JSON.stringify({ message: 'User created successfully', userId: newUser.id }), { status: 201 });

  } catch (error) {
    console.error('Registration Error:', error);
    return new Response(JSON.stringify({ message: 'Internal Server Error' }), { status: 500 });
  }
}
